  margin-right: 20px;
  cursor: pointer;
  position: relative;
}

.Header-Container .Cart img {
  display: block;
}

.Header-Container .Cart .Count {
  top: -20px;
  right: -25px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  color: white;
  background-color: #fa4a0c;
  padding: 3px 8px;
