.paginationBttns {
  width: 100%;
  height: 40px;
  margin: auto;
  padding: 0px;
  font-weight: 500;
  list-style: none;
  display: flex;
  align-items: center;
  justify-content: center;
  -webkit-margin-before: 50px;
          margin-block-start: 50px;
  -webkit-margin-after: 50px;
          margin-block-end: 50px;
}

.paginationBttns a {
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 2px solid rgb(14 14 14 / 12%);
  color: black;
  cursor: pointer;
  text-decoration: none;
  transition: 150ms;
}

.paginationBttns a:hover {
  color: white;
  background-color: #b9b9b9;
}

.paginationActive a {
  color: white;
  background-color: #b9b9b9;
}

.paginationDisabled a {
